/* Header Section */
.intro {
  display: table;
  position: relative;
  width: 100%;
  padding: 0;
  background-color: #e5e5e5;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  background-size: cover;
  -o-background-size: cover;
  height: auto;

  /* background-image: url("/img/blueAbstract.jpg"); */
}
.intro .overlay {
  background: rgba(0, 0, 0, 0.2);
}
.intro h1 {
  font-family: "Raleway", sans-serif;
  /* color: #fff; */
  font-size: 82px;
  font-weight: 700;
  text-transform: uppercase;
  margin-top: 0;
  margin-bottom: 10px;

  background: -webkit-linear-gradient(129deg, #09009f, #029b5b 80%);
  background-clip: text;
  -webkit-text-fill-color: transparent;
  margin: 0;
}
.intro h1 span {
  font-weight: 800;
  color: #5ca9fb;
}
.intro p {
  color: rgb(70, 70, 70);
  font-size: 22px;
  font-weight: 300;
  line-height: 30px;
  margin: 0 auto;
  margin-bottom: 60px;
}
header .intro-text {
  padding-top: 350px;
  padding-bottom: 200px;
  text-align: center;
}
